.all {
  /* height: 800px; */
  max-width: 500px;
  margin: auto;
  border-radius: 25px;
  border: #000 25px solid;
}
.main {
  background-color: #c9e4f5;
  margin: 0;
}
.header {
  background-color: #3a92cd;
}
.logo {
  color: #fff;
  display: block;
  padding: 20px;
  font-size: 20px;
  font-weight: 700;

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 450px) {
    margin-right: 40px;
  }
  @media screen and (min-width: 500px) {
    margin-right: 20px;
  }
}
.logo:hover {
  color: #266894;
}
.nav {
  display: flex;
  align-items: center;
}
.home {
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;

  /* align-self: flex-end; */
  font-weight: 700;
  font-size: 40px;
  line-height: 25px;
  border-radius: 50px;
  border: 1px solid #fff;
  padding: 10px;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.home:hover {
  color: #266894;
  border: 1px solid #266894;
  background-color: #ffffff2c;
}

.list {
  color: #fff;
  border-radius: 4px;
  font-size: 16px;
  padding: 18px;
  /* margin-left: 20px; */
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 450px) {
    margin-left: 40px;
  }
  @media screen and (min-width: 500px) {
    margin-left: 10px;
  }
}
.list:hover {
  color: #266894;
}
